.WelcomeScreen { 
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

h1.welcome-slogan, h4.welcome-instruction, a.privacy-policy{
    color: white;
}

span.app-title {
    color: blue;
}

span.app-title:hover {
    box-shadow: 0 0 5em #4285f4;
    text-decoration: underline 1px solid blue;
}

a.privacy-policy {
    text-decoration: none;
    cursor: pointer;
}
a.privacy-policy:hover {
    color: blue;
}

.login-button {
color: white;
text-transform: uppercase;
text-decoration: none; 
background: #ffffff;
border: 4px solid #494949;
display: inline-block;
transition: all 0.4s ease 0s;
min-width: 10rem;
margin: 1rem;
}

.google-btn {
width: 184px;
height: 42px;
margin: 1rem;
background-color: #4285f4;
border-radius: 2px;
box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover { 
    box-shadow: 0 0 5em #4285f4;
}

.google-btn:active { 
    background: #1669f2;
}

.google-icon-wrapper { 
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
}

.google-icon { 
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
}

.btn-sign-in {
float: right;
color: #fff;
font-size: 14px;
letter-spacing: 0.2px;
font-family: "Roboto";
background-color: #4285f4;
width: 80%;
height: 100%;
text-align: center; 
cursor: pointer;
}